import common from "../../common/common.js";
import helloComp from "../../components/helloComp.vue";
import trailerStars from "../../components/trailerStars.vue";

export default {
	data() {
		return {
			carouselList: [],
			hotList: [],
			trailerList: [],
			animationData: {},
			animationDataArr: [
				{},{},{},{},{}
			],
			guessULikeList: []
		}
	},

	onUnload() {
		this.animation = {};
		
		this.animationDataArr = [
			{},{},{},{},{}
		];
	},
	onPullDownRefresh() {
		this.refresh();
	},
	onLoad() {
		// var me = this;
		
		// var serverUrl = common.serverUrl;
		// debugger;
		// var me = this.carouselList;
		// 请求轮播图数据
		

	this.animation = uni.createAnimation();

		
	
		
		
		uni.request({
			url: this.serverUrl + '/index/carousel/list', //仅为示例，并非真实接口地址。
			data: {
				qq: this.import_key
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			method:"POST",
			success: (res) => {
				this.carouselList = res.data.data;
				// console.log(this.carouselList);
			}
		});
		
		// 查询热门超英
		uni.request({
			url: this.serverUrl + '/index/movie/hot?type=superhero&qq=' + this.import_key, //仅为示例，并非真实接口地址。


			method:"POST",
			success: (res) => {
				this.hotList = res.data.data;
			}
		});
		
		// 预告片
		uni.request({
			url: this.serverUrl + '/index/movie/hot?type=trailer&qq=' + this.import_key, //仅为示例，并非真实接口地址。
	
			method:"POST",
			success: (res) => {
				this.trailerList = res.data.data;
			}
		});
		
		//猜你喜欢
		uni.request({
			url: this.serverUrl + '/index/guessULike?qq=' + this.import_key, //仅为示例，并非真实接口地址。
				
			method:"POST",
			success: (res) => {
				this.guessULikeList = res.data.data;
	
			}
		});
		
		this.refresh();
	},
	methods: {
		refresh() {
			
			uni.showLoading({
				mask:true
			});
			uni.showNavigationBarLoading({
				
			});
			
			//猜你喜欢
			uni.request({
				url: this.serverUrl + '/index/guessULike?qq=' + this.import_key, //仅为示例，并非真实接口地址。
					
				method:"POST",
				success: (res) => {
					this.guessULikeList = res.data.data;
				},
				complete: ()=> {
					uni.hideLoading();
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
				}
			});
		},
		// 实现点赞动画效果
		praiseMe(e) {

			var index = e.currentTarget.dataset.index;
			
			this.animation.translateY(-60).opacity(1).step({
					duration:400,
				})
			this.animationData = this.animation;
			this.animationDataArr[index] = this.animation.export();
			
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration:0,
				});
				this.animationData = this.animation;
				this.animationDataArr[index] = this.animation.export();
			}.bind(this), 500);

		},
		
		showTrailer(e) {
			var trailerId = e.currentTarget.dataset.trailerid;
			uni.navigateTo({
				url: "../movie/movie?trailerId=" + trailerId
			})
		}
	},
	
	components:{
		helloComp,
		trailerStars
	}
}
